import React, { useState, useEffect } from 'react';
import { withStyles, useTheme, makeStyles } from '@material-ui/styles';
import { IconSearch } from '@tabler/icons';
import RemoveRedEyeIcon from '@mui/icons-material/RemoveRedEye';
import AddIcon from '@material-ui/icons/Add';
import GroupIcon from '@material-ui/icons/Group';
import AddEditUser from './AddEditUser';
import ConfirmDialog from '../../../ui-component/ConfirmDialog/ConfirmDialog';
import useStyles from '../styles';
import CloudUploadIcon from '@mui/icons-material/CloudUpload';
import {
    IconButton,
    Typography,
    Button,
    Table,
    TableBody,
    TableCell,
    TableContainer,
    TableHead,
    TableRow,
    Paper,
    TablePagination,
    OutlinedInput,
    InputAdornment,
    Tooltip,
    Grid,
    CircularProgress,
} from '@material-ui/core';
import { useDispatch, useSelector } from 'react-redux';
import { deleteUserProfile, getAllProfile } from '../../../store/actions/AddUserActions';
import { useForm } from 'react-hook-form';
import { useHistory } from 'react-router-dom';
import { csvUpload } from '../../../services/ProfileUpload';
import useUserRole from '../../../hooks/useUserRole';
import { setMessage } from '../../../store/actions/MessageActions';
import { getUserExport } from '../../../services/export';
import { Alert } from '@mui/material';
import DeleteForeverIcon from '@material-ui/icons/DeleteForever';
import '../../../notification/Notification.css'
import MainCard from '../../../ui-component/cards/MainCard';
const StyledTableRow = withStyles((theme) => ({
    root: {
        '&:nth-of-type(even)': {
            backgroundColor: theme.palette.action.hover
        }
    }
}))(TableRow);

export default function UserManagement() {
    const theme = useTheme();
 
    const classes = useStyles();
    useUserRole({ roleList: ['ADMIN', 'SCHOOL_ADMIN','SUPER_ADMIN'] });
    const dispatch = useDispatch();
    const history = useHistory();
    const [file, setFile] = useState(null);
    const [page, setPage] = useState(0);
    const [rowsPerPage, setRowsPerPage] = useState(25);
    const [addEditUserPopup, setAddEditUserPopup] = useState(false);
    const [uploading, setUploading] = useState(false);
    const [notification, setNotification] = useState('');

    const { loggedUserProfile } = useSelector((state) => state?.userProfile);
    const [confirmDialog, setConfirmDialog] = useState({ isOpen: false, title: '', subTitle: '' });

    const [isSearchUser, setSearchUser] = useState('');
    const { getAllProfileData } = useSelector((state) => state?.addUser);
    const newData = getAllProfileData?.content;

    const [loading, setLoading] = useState(false);
    const [dowmloadFail, setDownloadFail] = useState(false);

    const { reset } = useForm();

    const handleChangePage = (event, newPage) => {
        setPage(newPage);
    };

    const handleChangeRowsPerPage = (event) => {
        setRowsPerPage(+event.target.value);
    };

    const handleProfile = (id) => {
        history.push(`users/profile/${id}`);
    };

    const handleOpenModal = () => {
        reset();
        setAddEditUserPopup(true);
    };

    const handleSearchUser = (event) => {
        setSearchUser(event.target.value);
    };

    const showNotification = (message) => {
        setNotification(message);

        setTimeout(() => {
            setNotification('');
        }, 3000);
    };


    const handleDownload = () => {
        const link = document.createElement('a');
        link.href = 'https://mum-objectstore.e2enetworks.net/bookeinstein/SampleUser.csv';
        link.download = 'Sample User.csv';
        link.target = '_blank';

        link.dispatchEvent(new MouseEvent('click'));

        document.body.removeChild(link);
    };

    const CSVUpload = async () => {
        try {
            setUploading(true);
            const formData = new FormData();
            formData.append('files', file);
            await csvUpload(formData, dispatch);
            setTimeout(() => {
                window.location.reload();
                dispatch(dispatch(getAllProfile()));
            }, 2000);
            // showNotification('CSV uploaded successfully!');
            
        } catch (error) {
            console.log(error);
            setMessage(error?.data?.message)
        } finally {
            setUploading(false);
        }
    };


    const fileName = 'myfile';
    const exportUser = async () => {
        setDownloadFail(false);
        setLoading(true);

        await getUserExport()
            .then((r) => {
                setLoading(false);
                console.log(r);
            })
            .catch((error) => {
                setLoading(false);
                setDownloadFail(true);
                console.log(error);
            });
    };

    const handleCSV = (event) => {
        CSVUpload(dispatch);
    };

    const handleFileChange = async (event) => {
        const selectedFile = event?.target?.files[0];
        setFile(selectedFile);

        if (selectedFile) {
            try {
                setUploading(true);
                const formData = new FormData();
                formData.append('files', selectedFile);
                await csvUpload(formData, dispatch, setMessage);
                setTimeout(() => {
                    window.location.reload();
                    dispatch(dispatch(getAllProfile()));
                }, 2000);
            } catch (error) {
                console.log(error);
                setMessage(error?.data?.message)
            } finally {
                setUploading(false);
            }
        }
    };


    const handleDeleteUser = (id) => {
        const payload = {
            isDeleted: true
        };
        dispatch(
            deleteUserProfile(payload, id, () => {
                dispatch(getAllProfile(rowsPerPage, isSearchUser ? isSearchUser : '', page + 1));
            })
        );
        setConfirmDialog({
            ...confirmDialog,
            isOpen: false
        });
    };

    useEffect(() => {
        dispatch(getAllProfile(rowsPerPage, isSearchUser ? isSearchUser : '', page + 1));
    }, [rowsPerPage, isSearchUser, page]);
    

    return (
        <>
            <MainCard>
                <div className={classes.titleContainer}>
                    <GroupIcon className={classes.pageTitleIcon} />
                    <span className={classes.pageTitle}>&nbsp; Bookeinstein all users </span>
                </div>
                <Paper elevation={5} className={classes.root}>
                    <Grid container spacing={3}>
                        <Grid item xs={12} sm={6} md={3}>
                            <OutlinedInput
                                className={classes.searchControl}
                                id="input-search-header"
                                placeholder="Search"
                                onChange={(event) => handleSearchUser(event)}
                                startAdornment={
                                    <InputAdornment position="start">
                                        <IconSearch stroke={1.5} size="1rem" className={classes.startAdornment} />
                                    </InputAdornment>
                                }
                            />
                        </Grid>

                        <Grid item xs={12} sm={12} md={4}>
                            <div style={{ display: 'flex', alignItems: 'center' }}>
                                <input
                                    type="file"
                                    accept=".csv"
                                    onChange={(e) => handleFileChange(e)}
                                    style={{ display: 'none' }}
                                    id="file-input"
                                />
                                <label htmlFor="file-input">
                                    <Button
                                        variant="contained"
                                        component="span"
                                        style={{ boxShadow: '0 0.5em 0.5em 0.1em #68c2d9' }}
                                        startIcon={<CloudUploadIcon />}
                                    >
                                        Upload CSV
                                    </Button>
                                </label>
                                {uploading && <CircularProgress />}
                            </div>
                        </Grid>

                        <Grid item>
                            <Button
                                variant="contained"
                                color="primary"
                                style={{ boxShadow: '0 0.5em 0.5em 0.1em #68c2d9' }}
                                onClick={() => handleOpenModal()}
                            >
                                <AddIcon /> User
                            </Button>
                        </Grid>
                        <Grid item>
                            <Button
                                variant="contained"
                                color="primary"
                                style={{ boxShadow: '0 0.5em 0.5em 0.1em #68c2d9' }}
                                onClick={exportUser}
                            >
                                Export
                            </Button>
                        </Grid>
                        <Grid item>
                            <Button
                                variant="contained"
                                color="primary"
                                style={{ boxShadow: '0 0.5em 0.5em 0.1em #68c2d9' }}
                                onClick={handleDownload}
                            >
                                Download Format
                            </Button>
                        </Grid>
                        <Grid item>
                            {loading ? (
                                <>
                                    <h7>Downloading...&nbsp;</h7>
                                    {'  '}
                                    <CircularProgress />
                                </>
                            ) : (
                                <></>
                            )}
                            {dowmloadFail ? <Alert severity="error">Downloading Failed !</Alert> : <> </>}
                        </Grid>
                    </Grid>
                    <TableContainer className={classes.tableContainer}>
                        <Table sx={{ minWidth: 200 }} className={classes.table} aria-label="simple table">
                            <TableHead className={classes.tableHeaderCell}>
                                <StyledTableRow>
                                    <TableCell> Email </TableCell>
                                    <TableCell> Role </TableCell>
                                    <TableCell> Mobile </TableCell>
                                    <TableCell align="center"> Status </TableCell>
                                    <TableCell align="center"> Action </TableCell>
                                </StyledTableRow>
                            </TableHead>
                            {!getAllProfileData ? (
                                <caption style={{ textAlign: 'center' }}> Sorry! No data available </caption>
                            ) : (
                                <TableBody>
                                    {getAllProfileData?.content?.map((row) => (
                                        <StyledTableRow key={row?.id}>
                                            <TableCell> {row?.email} </TableCell>
                                            <TableCell> {row?.role.replace('_', ' ')} </TableCell>
                                            <TableCell> {row?.mobile || '-'} </TableCell>
                                            <TableCell align="center">
                                                <Typography
                                                    className={classes.status}
                                                    style={{
                                                        backgroundColor:
                                                            row?.status === 'ACTIVE'
                                                                ? theme.palette.success.main
                                                                : theme.palette.warning.dark
                                                    }}
                                                >
                                                    {row?.status}
                                                </Typography>
                                            </TableCell>
                                            <TableCell align="center">
                                                <Tooltip title="View profile">
                                                    <IconButton style={{ cursor: 'pointer' }} onClick={() => handleProfile(row?.id)}>
                                                        <RemoveRedEyeIcon />
                                                    </IconButton>
                                                </Tooltip>
                                                {loggedUserProfile?.role === 'SUPER_ADMIN' && (
                                                    <Tooltip title="Delete User ">
                                                        <IconButton
                                                            onClick={() => {
                                                                setConfirmDialog({
                                                                    isOpen: true,
                                                                    title: 'Delete User profile',
                                                                    subTitle: 'Are you sure you want to delete user profile?',
                                                                    onConfirm: () => {
                                                                        handleDeleteUser(row?.id);
                                                                    }
                                                                });
                                                            }}
                                                        >
                                                            <DeleteForeverIcon color="error" style={{ cursor: 'pointer' }} />
                                                        </IconButton>
                                                    </Tooltip>
                                                )}
                                            </TableCell>
                                        </StyledTableRow>
                                    ))}
                                </TableBody>
                            )}
                        </Table>
                    </TableContainer>
                    <TablePagination
                        labelRowsPerPage={<span>Rows:</span>}
                        labelDisplayedRows={({ from, to, count }) => {
                            return `Page: ${page + 1} (${from}-${to} of ${count})`;
                        }}
                        backIconButtonProps={{
                            color: 'secondary'
                        }}
                        nextIconButtonProps={{ color: 'secondary' }}
                        SelectProps={{
                            inputProps: {
                                'aria-label': 'page number'
                            }
                        }}
                        showFirstButton={true}
                        showLastButton={true}
                        rowsPerPageOptions={[25, 50, 100]}
                        component="div"
                        count={getAllProfileData?.totalElements || 0}
                        rowsPerPage={rowsPerPage}
                        page={page}
                        onPageChange={handleChangePage}
                        onRowsPerPageChange={handleChangeRowsPerPage}
                        sx={{
                            '.MuiTablePagination-displayedRows': {
                                marginBottom: 0
                            },
                            '.MuiTablePagination-selectLabel': {
                                marginBottom: 0
                            }
                        }}
                    />
                </Paper>

                {addEditUserPopup && <AddEditUser open={addEditUserPopup} setOpen={setAddEditUserPopup} />}

                {confirmDialog && <ConfirmDialog confirmDialog={confirmDialog} setConfirmDialog={setConfirmDialog} />}
            </MainCard>
        </>
    );
}
