import React, { useState, useEffect } from 'react';
import { useSelector } from 'react-redux';
import CloseIcon from '@material-ui/icons/Close';
import { useForm } from 'react-hook-form';
import * as yup from 'yup';
import { yupResolver } from '@hookform/resolvers/yup';

import Visibility from '@material-ui/icons/Visibility';
import VisibilityOff from '@material-ui/icons/VisibilityOff';
import { FormProvider, RHFTextField, RHFSelect } from '../../../components/hook-form';
import ErrorMsg from '../../../ui-component/messages/ErrorMsg';
import PerfectScrollbar from 'react-perfect-scrollbar';
import useStyles from '../styles';
import {
    FormControl,
    Button,
    Dialog,
    DialogActions,
    DialogContent,
    DialogTitle,
    Typography,
    Grid,
    Link,
    Divider,
    InputAdornment,
    IconButton,
    CircularProgress
} from '@material-ui/core';
import { useDispatch } from 'react-redux';
import { addUser, updateProfile, getAllProfile } from '../../../store/actions/AddUserActions';
import { getSelectAllSchool } from '../../../store/actions/SchoolActions';

const ITEM_HEIGHT = 48;
const ITEM_PADDING_TOP = 8;
const MenuProps = {
    PaperProps: {
        style: {
            maxHeight: ITEM_HEIGHT * 4.5 + ITEM_PADDING_TOP,
            width: 250
        }
    }
};
const STATUS_ITEMS = [
    { id: 0, status: 'ACTIVE' },
    { id: 1, status: 'INACTIVE' }
];
const ROLE_ITEMS = [
    { id: 0, role: 'ADMIN' },
    { id: 1, role: 'SCHOOL_ADMIN' },
    { id: 2, role: 'TEACHER' },
    { id: 3, role: 'STUDENT' }
];
const ROLE_ITEM = [{ id: 0, role: 'STUDENT' }];
const validationSchema = yup.object({
    email: yup.string().email('Must be a valid email').trim().required('Email is required'),
    password: yup.string('Enter user password').trim().required('User password is required'),
    dob: yup.string('Enter Date Of Birth').trim().required('Date Of Birth is required'),
    mobile: yup.string('Enter user mobile number').trim().required('Mobile number is required'),
    firstName: yup.string('Enter your first name').trim().required('First name is required'),
    lastName: yup.string('Enter your first name').trim().required('Last name is required'),
    status: yup.string(),
    role: yup.string('Select role').required('Role is required'),
    schoolId: yup.string('Select role').when('role', {
        is: 'SCHOOL_ADMIN',
        then: yup.string().required('School is required')
    })
});

export default function AddEditUser(props) {
    const { open, setOpen } = props;
    const dispatch = useDispatch();
    const { loggedUserProfile } = useSelector((state) => state?.userProfile);
    const classes = useStyles();
    const [showPassword, setShowPassword] = useState(false);
    const { getAllSchoolData } = useSelector((state) => state?.school);
    const schoolList = getAllSchoolData;
    const [page, setPage] = useState(0);
    const [rowsPerPage, setRowsPerPage] = useState(25);
    const [isSearchUser, setSearchUser] = useState('');
    const [loading, setLoading] = useState(false);
    const [error, setError] = useState();
    const initialValues = {
        email: '',
        password: '',
        mobile: '',
        firstName: '',
        lastName: '',
        status: 'ACTIVE',
        role: '',
        schoolId: '',
        dob: '',
        classAndSection: ''
    };
    const methods = useForm({
        mode: 'onChange',
        resolver: yupResolver(validationSchema),
        defaultValues: initialValues
    });
    const { reset, handleSubmit, watch } = methods;

    const hide = watch('role');
    const hide1 = watch('schoolId');
    const handlePopupClose = () => {
        reset();
        setOpen(false);
    };

    const onSubmit = async (data) => {
        setLoading(true);
        try {
            let modifiedPayload = {};
            if (data?.schoolId === '') {
                modifiedPayload = {
                    email: data?.email,
                    password: data?.password,
                    mobile: data?.mobile,
                    firstName: data?.firstName,
                    lastName: data?.lastName,
                    status: data?.status,
                    dob: data?.dob,
                    classAndSection: data?.classAndSection
                };
            } else {
                modifiedPayload = {
                    email: data?.email,
                    password: data?.password,
                    mobile: data?.mobile,
                    firstName: data?.firstName,
                    lastName: data?.lastName,
                    status: data?.status,
                    schoolId: data?.schoolId,
                    dob: data?.dob,
                    classAndSection: data?.classAndSection
                };
            }

            const role = data?.role;
            await dispatch(
                addUser(modifiedPayload, role, async () => {
                    await dispatch(getAllProfile(rowsPerPage, isSearchUser ? isSearchUser : '', page + 1));
                    reset();
                    setOpen(false);
                })
            );
        } catch (error) {
            setError(error);
            console.error('An error occurred:', error);
        }
    };

    useEffect(() => {
        dispatch(getSelectAllSchool());
    }, []);

    return (
        <div>
            <Dialog open={open} maxWidth="xs" classes={{ paper: classes.dialog }}>
                <DialogTitle className={classes.dialogTitle}>
                    <div className={classes.dialogTitleChild}>
                        <Typography style={{ fontSize: '20px' }}> Add User </Typography>
                        <Link to="#" onClick={handlePopupClose}>
                            <CloseIcon className={classes.closeIcon} />
                        </Link>
                    </div>
                </DialogTitle>
                <FormProvider methods={methods} onSubmit={handleSubmit(onSubmit)}>
                    <PerfectScrollbar className={classes.ScrollHeight}>
                        <DialogContent>
                            <Grid container spacing={2}>
                                {(loggedUserProfile?.role === 'ADMIN' || loggedUserProfile?.role === 'SUPER_ADMIN') && (
                                    <Grid item xs={12} sm={12}>
                                        <label className={classes.label}> Role </label>
                                        <RHFSelect name="role" label="">
                                            <option value="">Select Role</option>
                                            {ROLE_ITEMS?.map((item, index) => (
                                                <option key={index} value={item?.role}>
                                                    {item?.role}
                                                </option>
                                            ))}
                                        </RHFSelect>
                                    </Grid>
                                )}

                                {loggedUserProfile?.role === 'SCHOOL_ADMIN' && (
                                    <Grid item xs={12} sm={12}>
                                        <label className={classes.label}>Role</label>
                                        <RHFSelect name="role" label="">
                                            <option value="">Select Role</option>
                                            {ROLE_ITEM?.map((item, index) => (
                                                <option key={index} value={item?.role}>
                                                    {item?.role}
                                                </option>
                                            ))}
                                        </RHFSelect>
                                    </Grid>
                                )}

                                {(hide === 'SCHOOL_ADMIN' || hide === 'STUDENT') && (
                                    <Grid item xs={12} sm={12}>
                                        <label className={classes.label}> School Name </label>

                                        <RHFSelect name="schoolId" label="">
                                            <option value="Select a School" selected>
                                                Select a School
                                            </option>
                                            {schoolList?.map((item, index) => (
                                                <option key={index} value={item?.id}>
                                                    {item?.name}
                                                </option>
                                            ))}
                                        </RHFSelect>
                                    </Grid>
                                )}

                                {hide1 !== '' && hide1 !== 'Select a School' && hide === 'STUDENT' && (
                                    <Grid item xs={12}>
                                        <RHFTextField
                                            name="classAndSection"
                                            placeholder="Enter your class/section"
                                            className={classes.loginInput}
                                        />
                                    </Grid>
                                )}

                                <Grid item xs={12} sm={12}>
                                    <label className={classes.label}>
                                        Email id <span className={classes.mandatory}>*</span>
                                    </label>
                                    <RHFTextField
                                        name="email"
                                        autoComplete="off"
                                        placeholder="Email address"
                                        className={classes.loginInput}
                                    />
                                </Grid>
                                <Grid item xs={12} sm={12}>
                                    <label className={classes.label}>
                                        Password <span className={classes.mandatory}>*</span>
                                    </label>
                                    <RHFTextField
                                        className={classes.loginInput}
                                        name="password"
                                        placeholder="Password"
                                        type={showPassword ? 'text' : 'password'}
                                        autoComplete="off"
                                        InputProps={{
                                            endAdornment: (
                                                <InputAdornment position="end">
                                                    <IconButton onClick={() => setShowPassword(!showPassword)} edge="end">
                                                        {showPassword ? <Visibility /> : <VisibilityOff />}
                                                    </IconButton>
                                                </InputAdornment>
                                            )
                                        }}
                                    />
                                </Grid>
                                <Grid item xs={12} sm={12}>
                                    <label className={classes.label}>
                                        Date Of Birth <span className={classes.mandatory}>*</span>
                                    </label>
                                    <RHFTextField type="date" name="dob" placeholder="Date Of Birth" className={classes.loginInput} />
                                </Grid>
                                <Grid item xs={12} sm={12}>
                                    <label className={classes.label}>
                                        Mobile Number <span className={classes.mandatory}>*</span>
                                    </label>
                                    <RHFTextField name="mobile" placeholder="Mobile number" className={classes.loginInput} />
                                </Grid>
                                <Grid item xs={12} sm={12}>
                                    <label className={classes.label}>
                                        First Name <span className={classes.mandatory}>*</span>
                                    </label>
                                    <RHFTextField name="firstName" placeholder="First name" className={classes.loginInput} />
                                </Grid>
                                <Grid item xs={12} sm={12}>
                                    <label className={classes.label}>
                                        Last Name <span className={classes.mandatory}>*</span>
                                    </label>
                                    <RHFTextField name="lastName" placeholder="Last name" className={classes.loginInput} />
                                </Grid>
                                <Grid item xs={12} sm={12}>
                                    <label className={classes.label}>Status</label>
                                    <RHFSelect name="status" label="">
                                        {STATUS_ITEMS?.map((item, index) => (
                                            <option key={index} value={item?.status}>
                                                {item?.status}
                                            </option>
                                        ))}
                                    </RHFSelect>
                                </Grid>
                            </Grid>
                        </DialogContent>
                    </PerfectScrollbar>
                    <Divider style={{ marginTop: 15 }} />
                    <DialogActions className={classes.dialogAction}>
                        <Button variant="outlined" color="primary" className={classes.buttonCancel} onClick={handlePopupClose}>
                            Close
                        </Button>
                        <Button variant="contained" disabled={loading} type="submit" className={classes.buttonSubmit}>
                            {loading ? <CircularProgress size={24} style={{ color: 'white' }} /> : 'Add'}
                        </Button>
                    </DialogActions>
                </FormProvider>
            </Dialog>
        </div>
    );
}
